import validator from "validator"


const validateAccountRegister = (userName, email, password)=>{
    const message = {}
 
    
    if(validator.isEmpty(userName)){
        message.userNameVN = "Vui lng nhp tn ti khon!"
        message.userNameENG = "Plese enter user name!"
    }
    else{
        if(userName.length < 6){
            message.userNameVN = "Tn ti khon phi nhiu hn 6 k t!"
            message.userNameENG = "Plese enter user name!"
        }
    }
    var strongRegex = new RegExp("^(?=.{14,})(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*\\W).*$", "g");
    var mediumRegex = new RegExp("^(?=.{10,})(((?=.*[A-Z])(?=.*[a-z]))|((?=.*[A-Z])(?=.*[0-9]))|((?=.*[a-z])(?=.*[0-9]))).*$", "g");
    var enoughRegex = new RegExp("(?=.{8,}).*", "g");

    if (password.length === 0) {
        message.passwordVN = 'Vui lng nhp mt khu';
        message.passwordEND = '0';
    } 
    else if (false === enoughRegex.test(password)) {
        message.passwordVN = 'Nhiu hn 8 k t';
    }
    else if (!strongRegex.test(password) && !mediumRegex.test(password)) {
        message.passwordVN = 'Mt khu phi cha ch hoa ch thng hoc s';
        // message.passwordVN = 'Mt khu mnh';
        // message.color = 'green';
    }
    // else if (!strongRegex.test(password) && !mediumRegex.test(password)) {
    //     // message.passwordVN = 'Mt khu mnh';
    //     // message.color = 'green';
    // } else if (mediumRegex.test(password)) {
    //     // message.passwordVN = 'Mu khu trung bnh';
    //     // message.color = 'yellow';
    // } else {
    //     message.passwordVN = 'Mt khu phi cha ch hoa ch thng hoc s';
    // }

    if(validator.isEmpty(email)){
        message.emailVN = "Vui lng nhp email!"
        message.emailENG = "Please enter email!"
    }

    return message
}
export default validateAccountRegister