import validator from "validator"


const validateContact = (name, email, content)=>{
    const message = {}
 
    const validateEmail = (email) => {
        return email.match(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
      };

    if(validator.isEmpty(name)){
        message.nameVN = "Vui lng nhp tn ca bn!"
        message.nameENG = "Plese enter your name!"
    }
    if(validator.isEmpty(email)){
        message.emailVN = "Vui lng nhp email!"
        message.emailENG = "Please enter your email!"
    }
    else{
        if(!validateEmail(email)){
            message.emailVN = "Email khng hp l!"
            message.emailENG = "Email is not valid!"            
        }
    }
    if(validator.isEmpty(content)){
        message.contentVN = "Bn cha nhp ni dung!"
        message.contentENG = "Please enter content!"
    }
    else{
        
        if(content.length < 10){
            message.contentVN = "Ni dung khng hp l!"
            message.contentENG = "Content is not valid!"
        }else{
            if(content.includes('cmm') || content.includes('cmn') || content.includes('duy p trai')){
                message.contentVN = "ng c chi th m!"
                message.contentENG = "No profanity!!"
            }
        }
    }

    return message
}
export default validateContact